
//mobile phone
.container {
    margin: 0 auto;
    padding: 0 15px;
    width: 320px;
    background-color: blueviolet;
    height: 100px;
}

.row {
    margin: 0 15px;
    &:after,
    &:before {
        clear: both;
        content: ' ';
        display: table;
    }
}

@for $i from 1 through $grid-column-number1 {
    .col-xs-#{$i} {
        float: left;
        padding: 0 15px;
        width: (100% / $grid-column-number1) * $i;
    }
}
@for $i from 1 through $grid-column-number1 {
        .col-xs-pull-#{$i} {
            margin-right: (100% / $grid-column-number1) * $i;
        }
        .col-xs-push-#{$i} {
            margin-left: (100% / $grid-column-number1) * $i;
        }
    }

//tablet
@media (min-width: 768px) {
    .container {
        padding: 0;
        width: 750px;
    }
    @for $i from 1 through $grid-column-number1 {
        .col-sm-#{$i} {
            float: left;
            min-height: 1px;
            padding: 0 15px;
            width: (100% / $grid-column-number1) * $i;
        }
    }
    @for $i from 1 through $grid-column-number1 {
        .col-sm-pull-#{$i} {
            margin-right: (100% / $grid-column-number1) * $i;
        }
        .col-sm-push-#{$i} {
            margin-left: (100% / $grid-column-number1) * $i;
        }
    }
}


//screen min 992px
@media (min-width: 992px) {
    .container {
        width: 970px;
    }
    @for $i from 1 through $grid-column-number1 {
        .col-md-#{$i} {
            float: left;
            min-height: 1px;
            padding: 0 15px;
            width: (100% / $grid-column-number1) * $i;
        }
    }
    @for $i from 1 through $grid-column-number1 {
        .col-md-pull-#{$i} {
            margin-right: (100% / $grid-column-number1) * $i;
        }
        .col-md-push-#{$i} {
            margin-left: (100% / $grid-column-number1) * $i;
        }
    }
}


//screen min 1200px
@media (min-width: 1200px) {
    .container {
        width: 1170px;
    }
    @for $i from 1 through $grid-column-number1 {
        .col-lg-#{$i} {
            float: left;
            min-height: 1px;
            padding-left: 15px;
            width: (100% / $grid-column-number1) * $i;
        }
    }
    @for $i from 1 through $grid-column-number1 {
        .col-lg-pull-#{$i} {
            margin-right: (100% / $grid-column-number1) * $i;
        }
        .col-lg-push-#{$i} {
            margin-left: (100% / $grid-column-number1) * $i;
        }
    }
}
